import { GET_MY_ADS } from "@/api/api";
import dashboardAdsImg from '@/assets/ads/2.png';
import meSideMenu from "@/components/meSideMenu";
import { encode } from "js-base64";
export default {
  name: "ads",
  components: {
    meSideMenu
  },

  data() {
    return {
      dashboardAdsImg,
      myAdsData: [],
      page: 1,
      limit: 8,
      totalNum: 0
    };
  },

  mounted() {
    this.getMyAdsList(this.page, this.limit);
  },

  methods: {
    turnPurchase() {
      this.$router.push('/me/ads/platform');
    },

    getMyAdsList(page, limit) {
      let params = {
        token: localStorage.getItem('token'),
        page: page,
        limit: limit
      };
      GET_MY_ADS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.myAdsData = res.message.data; // console.log(res.message.data)

          this.totalNum = res.message.total;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    pageSizeChange(e) {
      console.log(e);
    },

    pageChange(e) {
      this.page = e;
      this.getMyAdsList(e, this.limit);
      console.log(e);
    },

    redeem(data) {
      let dataStr = JSON.stringify(data);
      this.$router.push({
        path: '/me/ads/redeem',
        query: {
          info: encode(dataStr)
        }
      });
    },

    pay() {
      this.$msgbox({
        title: "Notice",
        message: "Oops! Our system is down at the moment. Don’t worry our Account Manager has been notified and will be in touch with you soon.",
        dangerouslyUseHTMLString: false,
        type: "warning",
        center: true,
        showCancelButton: true,
        cancelButtonText: "Cancel",
        confirmButtonText: "Confirm",
        "round-button": true,

        callback(action) {
          console.log(action);

          if (action === 'confirm') {
            console.log('用户点击确定');
            window.open('https://salesiq.zoho.com/signaturesupport.ls?widgetcode=75672d291fd9d5fcab53ffa3194f32598809c21f9b5284cbaf3493087cdd2e0d1a2010ab7b6727677d37b27582c0e9c4', '_blank');
          }
        }

      });
    }

  }
};